function map(){
	
	function initMap(){
		const location = {lat: 61.184986, lng: 6.850078},
				options =  {
					center: location,
					zoom: 12
				};
		const map = new google.maps.Map(document.getElementById('map'), options);
		const marker = new google.maps.Marker({
			position: location,
			map: map,
			icon: {
				url: '../img/icons/marker_for_maps/location1.png'
			}
		 });
	}
	initMap();
}
export default map;



